.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    /* max-height: 400px; */
    min-width: 100px;
    background-color: white;
    z-index: 1;
    transition: .5s;
  }

.product:hover {
    transform: scale(1.03);
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
    background: #f0c14b; 
    /* button color  #f0c14b */
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    transition: .5s;
}

.product > button:hover {
    border: 1px solid black;
    transform: scale(1.1);
}

.product__info {
    /* height: 100px; */
    margin-bottom: 15px;
}

.product__rating {
    display: flex;
}