.checkoutProduct {
    display: flex;
    /* flex-direction: row; */
    margin: 20px;
    gap: 20px;
}

.checkoutProduct__info {
    padding-left: 20px;
}

.checkoutProduct__info > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
}

.checkoutProduct__image {
    object-fit: contain;
    width: 180px;
    height: 180px;
}
.checkoutProduct__rating {
    display: flex;
}

.checkoutProduct__title {
    width: 200%;
    font-size: 16px;
    font-weight: 600;
}